.btn {
  padding: 12px 26px 10px;
  border-radius: 5px;
  background-color: #00b2ff;
  border: 1px solid #00b2ff;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff; }
  .btn--white {
    background: transparent;
    border: 1px solid #ffffff;
    color: #ffffff; }
  .btn--blue {
    background: transparent;
    border: 1px solid #00b2ff;
    color: #00b2ff; }

.header {
  background-color: #1b2d37;
  background-image: url("../../img/promo-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }
  @media (min-resolution: 1.5dppx) {
    .header {
      background-image: url("../../img/promo-bg-2x.png"); } }

.header__wrap {
  display: flex;
  box-sizing: border-box;
  max-width: 1440px;
  padding: 33px 16px 33px 10px;
  margin: 0 auto; }

.header__logo {
  display: block;
  width: 202px;
  height: 52px;
  margin-right: auto; }

.header__logo img {
  width: 100%;
  height: auto; }

.header__contact {
  display: flex;
  align-items: center;
  max-width: 350px;
  margin-left: 81px; }

.header__contact-link {
  display: block;
  padding: 2px 0;
  padding-left: 40px;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: 0 50%;
  font-family: "PT Mono", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none; }
  .header__contact-link--email {
    background-image: url("../../img/icon-email.svg"); }
  .header__contact-link--phone {
    background-image: url("../../img/icon-phone.svg");
    background-position: 5px 50%; }

.header__call {
  display: flex;
  align-items: center;
  margin-left: 50px;
  max-width: 300px; }

.header__contact-link:hover, .header__contact-link:focus,
.header__call-btn:hover,
.header__call-btn:focus {
  opacity: 0.7; }

.header__contact-link:active,
.header__call-btn:active {
  opacity: 0.5; }

@media (max-width: 1023px) {
  .header {
    background-image: url("../../img/promo-bg-tablet.png"); } }
  @media (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .header {
      background-image: url("../../img/promo-bg-tablet-2x.png"); } }

@media (max-width: 1023px) {
  .header__wrap {
    display: flex;
    box-sizing: border-box;
    max-width: 1440px;
    padding: 19px 4px 19px 21px;
    margin: 0 auto; }
  .header__contact {
    margin-left: 32px; }
  .header__contact-link {
    width: 26px;
    height: 26px;
    padding: 0;
    font-size: 0;
    background-size: 26px 26px; }
    .header__contact-link--phone {
      background-position: center; }
  .header__call {
    display: none; } }

@media (max-width: 767px) {
  .header {
    background-image: url("../../img/promo-bg-mobile.png"); } }
  @media (max-width: 767px) and (min-resolution: 1.5dppx) {
    .header {
      background-image: url("../../img/promo-bg-mobile-2x.png"); } }

@media (max-width: 767px) {
  .header__wrap {
    padding: 16px 14px 16px 17px; }
  .header__logo {
    width: 38px;
    height: 38px; }
  .header__contact {
    margin-left: 22px; } }

body {
  min-height: 100vh;
  font-family: "PT Mono", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #666666; }
